import JSZip from 'jszip';

// CORS proxy for downloading files
const getCorsProxiedUrl = (url: string): string => {
    // Use the same proxy logic as API calls
    if (window.location.hostname === 'bytenite2.github.io') {
        return `https://thingproxy.freeboard.io/fetch/${url}`;
    }
    return url;
};

/**
 * Downloads a zip file and extracts image files from it
 * @param zipUrl - URL to the zip file
 * @returns Array of base64 data URLs for the images
 */
export const downloadAndExtractImages = async (zipUrl: string): Promise<string[]> => {
    try {
        console.log('Downloading zip file:', zipUrl);
        
        // Try to download with CORS proxy first, then direct
        let response: Response;
        try {
            response = await fetch(getCorsProxiedUrl(zipUrl));
        } catch (corsError) {
            console.log('CORS proxy failed, trying direct download...');
            response = await fetch(zipUrl);
        }
        
        if (!response.ok) {
            throw new Error(`Failed to download zip file: ${response.status} ${response.statusText}`);
        }
        
        const zipBlob = await response.blob();
        console.log('Zip file downloaded, size:', zipBlob.size, 'bytes');
        
        // Load the zip file
        const zip = new JSZip();
        const loadedZip = await zip.loadAsync(zipBlob);
        
        const imageDataUrls: string[] = [];
        const imageExtensions = ['.png', '.jpg', '.jpeg', '.gif', '.bmp', '.webp'];
        
        // Extract image files
        for (const [filename, file] of Object.entries(loadedZip.files)) {
            // Skip directories
            if (file.dir) continue;
            
            // Check if it's an image file
            const isImage = imageExtensions.some(ext => 
                filename.toLowerCase().endsWith(ext)
            );
            
            if (isImage) {
                console.log('Extracting image:', filename);
                
                try {
                    // Get the file as blob
                    const imageBlob = await file.async('blob');
                    
                    // Convert to data URL
                    const dataUrl = await new Promise<string>((resolve, reject) => {
                        const reader = new FileReader();
                        reader.onload = () => resolve(reader.result as string);
                        reader.onerror = reject;
                        reader.readAsDataURL(imageBlob);
                    });
                    
                    imageDataUrls.push(dataUrl);
                } catch (error) {
                    console.error(`Failed to extract ${filename}:`, error);
                }
            }
        }
        
        console.log(`Extracted ${imageDataUrls.length} images from zip`);
        return imageDataUrls;
        
    } catch (error) {
        console.error('Error downloading/extracting zip:', error);
        throw error;
    }
};

/**
 * Downloads and extracts images from multiple zip files
 * @param zipUrls - Array of zip file URLs
 * @param onProgress - Progress callback function
 * @returns Array of base64 data URLs for all images
 */
export const downloadAndExtractMultipleZips = async (
    zipUrls: string[], 
    onProgress?: (current: number, total: number) => void
): Promise<string[]> => {
    const allImages: string[] = [];
    
    for (let i = 0; i < zipUrls.length; i++) {
        try {
            console.log(`Processing zip file ${i + 1}/${zipUrls.length}`);
            
            if (onProgress) {
                onProgress(i + 1, zipUrls.length);
            }
            
            const images = await downloadAndExtractImages(zipUrls[i]);
            allImages.push(...images);
        } catch (error) {
            console.error(`Failed to process zip file ${i + 1}:`, error);
            // Continue with other zip files even if one fails
        }
    }
    
    return allImages;
};
